import { Component, h, Host, Prop } from '@stencil/core';

@Component({
  tag: 'ks-order-summary-payment',
  styleUrl: 'summaryPayment.css'
})
export class OrderSummaryPayment {
    @Prop() text: string = "";
    @Prop() image: string = "";

    @Prop() color: string = "";
    @Prop() hover: string = "";
    @Prop() active: string = "";

    unique: string;
    componentWillLoad() {
        this.unique = (Date.now().toString(36) + Math.random().toString(36).slice(2, 6)).replace(/\d/g, '')
    }

    render() {
        return <Host>
            <style innerHTML={`
                ks-order-summary-payment .${this.unique} button {
                    background-color: ${this.color} !important;
                    color: white;
                }
                ks-order-summary-payment .${this.unique} button:hover {
                    background-color: ${this.hover} !important;
                }
                ks-order-summary-payment .${this.unique} button:active {
                    background-color: ${this.active} !important;
                }
            `}></style>

            <ks-button round submit name={this.text} image={this.image} class={this.unique} />
        </Host>
    }
}